@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700;900&display=swap);
@media screen and (max-width: 1024px){#header .columns{margin:0}#header .column{margin:auto !important;padding:0.5rem}#header .column:nth-child(1){padding-top:1rem}#main{text-align:center}#main .wrapper{width:90% !important}}body{font-family:"Merriweather", serif}p{line-height:30px}#header{-webkit-align-items:center;align-items:center;background:#632aff;color:white;text-align:center}#header h2{font-weight:bold}#header .app{background:rgba(255,255,255,0.089);border-radius:5px;font-size:2rem}#header .app span{background:white;border-radius:5px;box-shadow:0px 0px 10px white;color:#632aff;margin-left:1px;padding:0.05rem}#main{text-align:center}#main .wrapper{border-radius:5px;border:1px solid #632aff;margin:2.5rem auto 1rem;padding:1rem;width:50%;box-shadow:0px 0px 10px #632aff}#main .title{font-size:1.3rem}#main .subtitle{margin:0;font-size:1rem}#main .column .subtitle{margin-top:1rem}#main .actualWeather{font-size:1.5rem}#weather{width:90%;margin:auto}#weather .column{margin:0}#weather .is-three-fifths p{font-size:1.2rem}#weather .is-three-fifths p b{color:#632aff}.lds-ring{display:inline-block;position:absolute;width:80px;height:80px;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.lds-ring div{box-sizing:border-box;display:block;position:absolute;width:64px;height:64px;margin:8px;border:8px solid #632aff;border-radius:50%;-webkit-animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;animation:lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;border-color:#632aff transparent transparent transparent}.lds-ring div:nth-child(1){-webkit-animation-delay:-0.45s;animation-delay:-0.45s}.lds-ring div:nth-child(2){-webkit-animation-delay:-0.3s;animation-delay:-0.3s}.lds-ring div:nth-child(3){-webkit-animation-delay:-0.15s;animation-delay:-0.15s}@-webkit-keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes lds-ring{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

